.footer {
  background-color: var(--darkModeBackgroundColor);
  padding: 40px 0;
  color: var(--lightModeBackgroundColor);
}
[data-theme="light"] .footer {
  background-color: var(--lightModeBackgroundColor);
  color: var(--darkModeBackgroundColor);
}

.footer-content {
  width: 1024px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1024px;
  margin: 0 auto;
}

.footer-left {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.footer-logo {
  font-size: 40px;
  font-weight: bold;
}

.footer-links {
  display: flex;
  gap: 40px;
}

.footer-column h3 {
  font-size: 18px;
  margin-bottom: 10px;
}

.footer-column ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-column ul li {
  font-size: 16px;
  margin-bottom: 8px;
  cursor: pointer;
}
.footer-column ul li a {
  color: inherit;
  text-decoration: none;
  transition: color 0.3s;
}

.footer-column ul li a:hover {
  color: var(--basicDarkGray);
}

.footer-bottom {
  text-align: left;
  margin-top: 20px;
  font-size: 14px;
  max-width: 1024px;
  margin: 20px auto 0;
}

@media (max-width: 900px) {
  .footer-content {
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
  }

  .footer-left,
  .footer-links {
    align-items: center;
    justify-content: center;
  }

  .footer-links {
    flex-direction: column;
    gap: 20px;
    margin-top: 20px;
  }

  .footer-column {
    text-align: center;
  }

  .footer-bottom {
    text-align: center;
  }
}

@media (max-width: 480px) {
  .footer-logo {
    font-size: 32px;
  }
  .footer-column h3 {
    font-size: 16px;
  }
  .footer-column ul li {
    font-size: 14px;
  }
  .footer-bottom {
    font-size: 13px;
  }
}
